import {
  Grid,
  MenuItem,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  TextField,
} from "@material-ui/core";
import React, { FC, memo, useEffect } from "react";

import { Line, Pie, Doughnut } from "react-chartjs-2";
import { useDispatch, useSelector } from "react-redux";
import img_chart from "../../../Assets/Images/Icons/chart.png";
import img_female from "../../../Assets/Images/Icons/female.png";
import img_male from "../../../Assets/Images/Icons/male.png";
import { setPageLinks } from "../../../Services/Actions/PageActions";
import "chartjs-plugin-labels";
import FamilyActions from "../../../Services/Actions/FamilyActions";
import { RootStore } from "../../../Services/Store";
import CustomAvatar from "../../../Component/CustomAvatar";
import LinearLoadingProgress from "../../../Component/LinearLoadingProgress";
import DashboardActions from "../../../Services/Actions/DashboardActions";
import CircularLoadingProgress from "../../../Component/CircularLoadingProgress";
import { YearlyStatsModel } from "../../../Services/Models/DashboardModel";

interface IDashbboardAdminView {}

export const DashbboardAdminView: FC<IDashbboardAdminView> = memo(() => {
  const dispatch = useDispatch();

  const fam_table = useSelector(
    (store: RootStore) => store.FamilyReducer.fam_table
  );
  const set_fam_table = useSelector(
    (store: RootStore) => store.FamilyReducer.set_fam_table
  );

  const overall_population = useSelector(
    (store: RootStore) => store.DashboardReducer.overall_population
  );

  const fetch_overall_population = useSelector(
    (store: RootStore) => store.DashboardReducer.fetch_overall_population
  );

  const gender_stats = useSelector(
    (store: RootStore) => store.DashboardReducer.gender_stats
  );

  const fetch_gender_stats = useSelector(
    (store: RootStore) => store.DashboardReducer.fetch_gender_stats
  );

  const age_group_stats = useSelector(
    (store: RootStore) => store.DashboardReducer.age_group_stats
  );
  const fetch_age_group_stats = useSelector(
    (store: RootStore) => store.DashboardReducer.fetch_age_group_stats
  );

  const life_stage_stats = useSelector(
    (store: RootStore) => store.DashboardReducer.life_stage_stats
  );
  const fetch_life_stage_stats = useSelector(
    (store: RootStore) => store.DashboardReducer.fetch_life_stage_stats
  );

  useEffect(() => {
    dispatch(FamilyActions.getAllFamily());
    dispatch(DashboardActions.setOverallPopulation());
    dispatch(DashboardActions.setAgeGroupStats());
    dispatch(DashboardActions.setGenderStats());
    dispatch(DashboardActions.setLifeStageStats());
  }, [dispatch]);

  useEffect(() => {
    let mounted = true;

    const settingPageLinks = () => {
      dispatch(
        setPageLinks([
          {
            link: "/admin/dashboard",
            title: "Dashboard",
          },
        ])
      );
    };

    mounted && settingPageLinks();
    return () => (mounted = false);
  }, [dispatch]);

  return (
    <>
      <div style={{ padding: `0 1.5em` }}>
        <Grid container spacing={4}>
          <Grid item container spacing={2} xs={12}>
            <Grid item xs={6} md={3}>
              <div className="stats-item">
                <div className="value">3</div>
                <div className="label">TIBUOK PAPOLASYON</div>
              </div>
            </Grid>

            <Grid item xs={6} md={3}>
              <div className="stats-item">
                <div className="value">3</div>
                <div className="label">NAMATAY</div>
              </div>
            </Grid>
            <Grid item xs={6} md={3}>
              <div className="stats-item">
                <div className="value">3</div>
                <div className="label">PWD</div>
              </div>
            </Grid>
            <Grid item xs={6} md={3}>
              <div className="stats-item">
                <div className="value">3</div>
                <div className="label">Senior Citizen</div>
              </div>
            </Grid>
          </Grid>
          <Grid item xs={6}>
            <div
              style={{
                backgroundColor: `#fff`,
                padding: `1em`,
                paddingTop: 0,
                borderRadius: 10,
                height: `100%`,
              }}
            >
              <div className="title">Overall Statistics</div>
              {fetch_overall_population || !overall_population ? (
                <CircularLoadingProgress />
              ) : (
                <Line
                  type="line"
                  data={{
                    labels: overall_population.labels,
                    datasets: [
                      {
                        label: "Buhi",
                        yAxesGroup: "1",
                        fill: false,
                        fillColor: "blue",
                        strokeColor: "blue",
                        highlightFill: "blue",
                        highlightStroke: "blue",
                        borderColor: "blue",
                        scales: {
                          yAxes: [
                            {
                              stacked: true,
                            },
                          ],
                        },
                        data: overall_population.alive,
                      },
                      {
                        label: "Namatay",
                        yAxesGroup: "2",
                        fill: false,
                        fillColor: "red",
                        strokeColor: "red",
                        highlightFill: "red",
                        highlightStroke: "red",
                        borderColor: "red",
                        scales: {
                          yAxes: [
                            {
                              stacked: true,
                            },
                          ],
                        },
                        data: overall_population.death,
                      },
                    ],
                  }}
                  options={{
                    responsiveAnimationDuration: 1,
                    scales: {
                      yAxes: [
                        {
                          scaleLabel: {
                            display: true,
                            labelString: "Population",
                            lineHeight: 2,
                            fontColor: `#333`,
                            fontFamily: `Nunito`,
                          },
                          ticks: {
                            beginAtZero: true,
                            userCallback: function (label, index, labels) {
                              // when the floored value is the same as the value we have a whole number
                              if (Math.floor(label) === label) {
                                return label;
                              }
                            },
                          },
                        },
                      ],
                    },
                  }}
                />
              )}
            </div>
          </Grid>
          <Grid item xs={6}>
            <div
              style={{
                backgroundColor: `#fff`,
                padding: `1em`,
                paddingTop: 0,
                borderRadius: 10,
                height: `100%`,
              }}
            >
              <div className="title">Yearly Statistics</div>
              {/*               
              <div>
                <TextField
                  variant="outlined"
                  label="Pili og Tuig na Ipakita"
                  select
                  InputLabelProps={{
                    shrink: true,
                  }}
                  style={{
                    width: `200px`,
                  }}
                >
                  <MenuItem value="2021">2021</MenuItem>
                  <MenuItem value="2020">2020</MenuItem>
                  <MenuItem value="2019">2019</MenuItem>
                  <MenuItem value="2018">2018</MenuItem>
                  <MenuItem value="2017">2017</MenuItem>
                </TextField>
              </div>
            */}
              {fetch_age_group_stats || !age_group_stats ? (
                <CircularLoadingProgress />
              ) : (
                <Line
                  type="line"
                  data={{
                    labels: age_group_stats.labels,
                    datasets: [
                      {
                        label: "Han-ay sa Edad",
                        fillColor: "blue",
                        strokeColor: "blue",
                        highlightFill: "blue",
                        highlightStroke: "blue",
                        borderColor: "blue",
                        scales: {
                          yAxes: [
                            {
                              stacked: true,
                            },
                          ],
                        },
                        data: age_group_stats.data_set,
                      },
                    ],
                  }}
                  options={{
                    responsiveAnimationDuration: 1,
                    scales: {
                      yAxes: [
                        {
                          scaleLabel: {
                            display: true,
                            labelString: "Population",
                            lineHeight: 2,
                            fontColor: `#333`,
                            fontFamily: `Nunito`,
                          },
                          ticks: {
                            beginAtZero: true,
                            userCallback: function (label, index, labels) {
                              // when the floored value is the same as the value we have a whole number
                              if (Math.floor(label) === label) {
                                return label;
                              }
                            },
                          },
                        },
                      ],
                    },
                  }}
                />
              )}
            </div>
          </Grid>

          <Grid item xs={12} sm={8}>
            <div
              style={{
                backgroundColor: `#fff`,
                padding: `1em`,
                paddingTop: 0,
                borderRadius: 10,
                height: `100%`,
              }}
            >
              <div className="title">Proporsiyon sa Sekso</div>

              {fetch_gender_stats || !gender_stats ? (
                <CircularLoadingProgress />
              ) : (
                <div className="sekso-ctnr">
                  <div className="sekso-item">
                    <img className="dp" src={img_female} alt="" />
                    <div className="label">BABAE</div>
                    <div className="value">{gender_stats.data_set[1].y}</div>
                    <img className="graph" src={img_chart} alt="" />
                  </div>
                  <div className="sekso-item">
                    <img className="dp" src={img_male} alt="" />
                    <div className="label">LALAKI</div>
                    <div className="value">{gender_stats.data_set[0].y}</div>
                    <img className="graph" src={img_chart} alt="" />
                  </div>

                  <div style={{ justifySelf: `center`, alignSelf: `center` }}>
                    <Doughnut
                      data={{
                        labels: gender_stats.labels,
                        datasets: [
                          {
                            labels: gender_stats.labels,
                            data: findValuesOfStats(gender_stats.data_set),
                            backgroundColor: ["#5c6bc0", "#66bb6a"],
                            borderColor: "#fff",
                          },
                        ],
                      }}
                      options={{
                        responsiveAnimationDuration: 1,
                        tooltips: {
                          enabled: false,
                        },
                        plugins: {
                          labels: {
                            render: "percentage",
                            precision: 0,
                            showZero: true,
                            fontSize: 12,
                            fontColor: "#fff",
                          },
                        },
                      }}
                    />
                  </div>
                </div>
              )}
            </div>
          </Grid>
          <Grid item xs={12} sm={4}>
            <div
              style={{
                backgroundColor: `#fff`,
                padding: `1em`,
                paddingTop: 0,
                borderRadius: 10,
                height: `100%`,
              }}
            >
              <div className="title">Proporsyon sa Edad</div>
              <div
                style={{
                  padding: `1em`,
                }}
              >
                {fetch_life_stage_stats || !life_stage_stats ? (
                  <CircularLoadingProgress />
                ) : (
                  <Pie
                    type="pie"
                    height={200}
                    data={{
                      labels: life_stage_stats.labels,
                      datasets: [
                        {
                          labels: life_stage_stats.labels,
                          data: findValuesOfStats(life_stage_stats.data_set),
                          backgroundColor: ["#ef5350", "#66bb6a", "#5c6bc0"],
                          borderColor: "#fff",
                        },
                      ],
                    }}
                    options={{
                      responsiveAnimationDuration: 1,
                      tooltips: {
                        enabled: false,
                      },
                      plugins: {
                        labels: {
                          render: "percentage",
                          precision: 0,
                          showZero: true,
                          fontSize: 12,
                          fontColor: "#fff",
                        },
                      },
                    }}
                  />
                )}
              </div>
            </div>
          </Grid>

          <Grid item xs={12}>
            <div
              style={{
                backgroundColor: `#fff`,
                padding: `1em`,
                paddingTop: 0,
                borderRadius: 10,
                height: `100%`,
              }}
            >
              <div className="title">Mga Pamilya sa Barangay</div>

              <TableContainer>
                <LinearLoadingProgress show={set_fam_table} />
                <Table>
                  <TableHead>
                    <TableRow>
                      <TableCell>Ulo sa Pamilya</TableCell>
                      <TableCell>Purok</TableCell>
                      <TableCell>Tuig sa Pagpuyo</TableCell>
                      <TableCell>Straktura sa Balay</TableCell>
                      <TableCell>Kalig-on sa Balay</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {fam_table?.map((f, i) => (
                      <TableRow>
                        <TableCell>
                          <div className="table-cell-profile">
                            <CustomAvatar
                              className="image"
                              variant="circle"
                              src={`${f.ulo_pamilya_info?.pic}`}
                              errorMessage={`${f.ulo_pamilya_info?.first_name?.charAt(
                                0
                              )}${f.ulo_pamilya_info?.last_name?.charAt(0)}`}
                            />
                            <div className="title">
                              <span style={{ textTransform: "capitalize" }}>
                                {f.ulo_pamilya_info.last_name} {", "}
                                {f.ulo_pamilya_info.first_name}{" "}
                                {f.ulo_pamilya_info.middle_name}{" "}
                                {f.ulo_pamilya_info.suffix}
                              </span>
                            </div>
                          </div>
                        </TableCell>
                        <TableCell>
                          <b>{f.ulo_pamilya_info.purok}</b>
                        </TableCell>
                        <TableCell>
                          <small>{f.kadugayon_pagpuyo}</small>
                        </TableCell>
                        <TableCell>
                          <small>{f.straktura}</small>
                        </TableCell>
                        <TableCell>
                          <small>{f.kaligon_balay}</small>
                        </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </TableContainer>
            </div>
          </Grid>
        </Grid>
      </div>
    </>
  );
});

export default DashbboardAdminView;

const findValuesOfStats = (
  data_set: Array<YearlyStatsModel>
): Array<number | string> => {
  const values: Array<number | string> = [];

  data_set.forEach((d) => {
    values.push(d.y);
  });

  return values;
};
