import {
  Checkbox,
  FormControl,
  FormControlLabel,
  FormGroup,
  FormHelperText,
  FormLabel,
  Radio,
  RadioGroup,
} from "@material-ui/core";
import { setNestedObjectValues } from "formik";
import React, { FC, memo } from "react";
import { Controller, useFieldArray, useFormContext } from "react-hook-form";

interface RadioItems {
  label: string;
  value: string | number;
  disabled?: boolean;
  defaultCheck?: boolean;
}

interface MultCheckboxHookFormProps {
  //extends Partial<> {
  name: string;
  label: string;
  variant?: "standard" | "outlined" | "filled";
  row?: boolean;
  radio_items: Array<RadioItems>;
}

export const MultCheckboxHookForm: FC<MultCheckboxHookFormProps> = memo(
  (props) => {
    const { control, errors, getValues, setValue } = useFormContext();

    let error = false;
    let error_message = "";

    if (errors && errors?.hasOwnProperty(props?.name)) {
      error = true;
      error_message = errors[props?.name]?.message;
    }

    return (
      <FormControl error={error} variant={props.variant} component="fieldset">
        <FormLabel component="legend">{props.label}</FormLabel>
        <FormGroup row={props.row}>
          {props.radio_items.map((field, index) => {
            return (
              <FormControlLabel
                key={props.name + `.[${index}`}
                label={field.label}
                control={
                  <Controller
                    name={props.name + `.[${index}`}
                    control={control}
                    render={(props) => (
                      <Checkbox
                        {...props}
                        checked={props.value === field.value ? true : false}
                        // checked={props.value}
                        // defaultChecked={!!props.value}
                        onChange={(e) => {
                          props.onChange(
                            e.target.checked ? field.value : false
                          );

                          // const array_new_val: Array<any> = [];

                          // const vals = getValues();

                          // const got_values: Array<any> = vals?.sts_pk;

                          // if (got_values) {
                          //   const filtered_val = got_values.filter(
                          //     (v) => v !== false
                          //   );

                          //   console.log(`filtered_val`, filtered_val);

                          //   setValue("sts_pk", filtered_val, {
                          //     shouldDirty: true,
                          //     shouldValidate: true,
                          //   });
                          // }
                        }}
                      />
                    )}
                  />
                }
              />
            );
          })}
        </FormGroup>
        <FormHelperText>{error_message}</FormHelperText>
      </FormControl>
    );
  }
);

export default MultCheckboxHookForm;
