import { PostFetch } from "../../Hooks/UseFetch";
import IServerResponse from "../Interface/IServerResponse";

const API_DEFAULT_ROUTE = `api/dashboard/`;

const overallPopulation = async (): Promise<IServerResponse> => {
  const response = await PostFetch(
    API_DEFAULT_ROUTE + "overallPopulation",
    null
  );
  return response;
};

const ageGroupStats = async (): Promise<IServerResponse> => {
  const response = await PostFetch(API_DEFAULT_ROUTE + "ageGroupStats", null);
  return response;
};

const genderStats = async (): Promise<IServerResponse> => {
  const response = await PostFetch(API_DEFAULT_ROUTE + "genderStats", null);
  return response;
};

const lifeStageStats = async (): Promise<IServerResponse> => {
  const response = await PostFetch(API_DEFAULT_ROUTE + "lifeStageStats", null);
  return response;
};

export default {
  overallPopulation,
  ageGroupStats,
  genderStats,
  lifeStageStats,
};
