import { yupResolver } from "@hookform/resolvers/yup";
import { Button, Chip, Grid } from "@material-ui/core";
import React, { FC, memo, useCallback, useEffect, useState } from "react";
import { FormProvider, useForm } from "react-hook-form";
import styled from "styled-components";
import * as yup from "yup";
import FormDialog from "../../../Component/FormDialog/FormDialog";
import DateFieldHookForm from "../../../Component/HookForm/DateFieldHookForm";
import MultCheckboxHookForm from "../../../Component/HookForm/MultCheckboxHookForm";
import TextFieldHookForm from "../../../Component/HookForm/TextFieldHookForm";
import ITableInitialSort from "../../../Services/Interface/ITableInitialSort";
import { ScrollPaginationModel } from "../../../Services/Models/PaginationModels";
interface IFilterDtComplaintAdminView {
  handleSetTableFilter: (table_filter: ScrollPaginationModel) => void;
  table_filter: ScrollPaginationModel;
  handleRefetchTable: () => void;
  open_filter_dialog: boolean;
  handleOpenFilterDialog: (open: boolean) => void;
}

const initialTableSort: Array<ITableInitialSort> = [
  {
    label: "Pinakauna gi-report",
    value: {
      column: "reported_at",
      direction: "desc",
    },
  },
  {
    label: "Pinakaulahi gi-report",
    value: {
      column: "reported_at",
      direction: "asc",
    },
  },
  {
    label: "Titulo (a-z)",
    value: {
      column: "title",
      direction: "asc",
    },
  },
  {
    label: "Titulo (z-a)",
    value: {
      column: "title",
      direction: "desc",
    },
  },
];

export const FilterDtComplaintAdminView: FC<IFilterDtComplaintAdminView> = memo(
  ({
    handleSetTableFilter,
    table_filter,
    handleRefetchTable,
    open_filter_dialog,
    handleOpenFilterDialog,
  }) => {
    // const [open_dialog, set_open_dialog] = useState(false);

    const form_add_news = useForm({
      mode: "onChange",
      // resolver: yupResolver(schema),
      defaultValues: table_filter.filters,
    });

    const [selected_sort, set_selected_sort] = useState(0);

    const handleChangeSort = useCallback((index: number) => {
      set_selected_sort(index);
    }, []);

    const handleSubmitForm = useCallback(
      (data) => {
        //       console.log(`data`, data);

        // data.sts_pk = data.sts_pk.filter((v) => !!v);

        const payload: ScrollPaginationModel = {
          filters: {
            ...data,
          },
          sort: initialTableSort[selected_sort].value,
        };

        console.log(`payload`, payload);

        handleSetTableFilter(payload);
        handleRefetchTable();
        handleOpenFilterDialog(false);
      },
      [
        handleOpenFilterDialog,
        handleRefetchTable,
        handleSetTableFilter,
        selected_sort,
      ]
    );

    // useEffect(() => {
    //   form_add_news.reset(table_filter.filters);
    // }, []);

    return (
      <>
        <FormDialog
          title="Isala ang resulta sa mga reklamo"
          handleClose={() => handleOpenFilterDialog(false)}
          open={open_filter_dialog}
          minWidth={600}
          body={
            <StyledFilter>
              <FormProvider {...form_add_news}>
                <form
                  onSubmit={form_add_news.handleSubmit(handleSubmitForm)}
                  noValidate
                  id="form-filter-records"
                >
                  <Grid container justify="center" spacing={2}>
                    <Grid item xs={12}>
                      <div className="filter-main-title">Pagpangita</div>
                      <TextFieldHookForm
                        name="search"
                        variant="outlined"
                        fullWidth={true}
                        placeholder="Ibutang ang mga letra sa titulo nga gusto makita"
                        InputLabelProps={{
                          shrink: true,
                        }}
                      />
                    </Grid>

                    <Grid item xs={12}>
                      <div className="filter-main-title">Matang</div>
                      <Grid container spacing={1}>
                        {initialTableSort.map((s, i) => (
                          <Grid key={i} item>
                            <Chip
                              label={s.label}
                              clickable
                              color={
                                selected_sort === i ? "primary" : "default"
                              }
                              onClick={() => {
                                handleChangeSort(i);
                              }}
                            />
                          </Grid>
                        ))}
                      </Grid>
                    </Grid>
                    <Grid item xs={12}>
                      <div className="filter-main-title">Pagsala</div>
                      <Grid container spacing={2}>
                        <Grid item xs={12}>
                          <MultCheckboxHookForm
                            label="Kahimtang"
                            name="sts_pk"
                            row={true}
                            radio_items={[
                              {
                                label: "Pending",
                                value: "P",
                              },
                              {
                                label: "On-progress",
                                value: "OP",
                              },
                              {
                                label: "Closed",
                                value: "C",
                              },
                            ]}
                          />
                        </Grid>
                        <Grid item xs={6}>
                          <DateFieldHookForm
                            type="date"
                            name="date_from"
                            label="Mahitabo Gikan"
                            InputLabelProps={{
                              shrink: true,
                            }}
                            clearable
                            fullWidth
                            inputVariant="outlined"
                            autoOk
                            defaultValue={null}
                          />
                        </Grid>
                        <Grid item xs={6}>
                          <DateFieldHookForm
                            type="date"
                            name="date_to"
                            label="Mahitabo Hangtod"
                            InputLabelProps={{
                              shrink: true,
                            }}
                            clearable
                            fullWidth
                            inputVariant="outlined"
                            autoOk
                            defaultValue={null}
                          />
                        </Grid>
                      </Grid>
                    </Grid>
                  </Grid>
                </form>
              </FormProvider>
            </StyledFilter>
          }
          actions={
            <>
              <Button type="reset" onClick={() => {}} variant="contained">
                Reset
              </Button>

              <Button
                variant="contained"
                form="form-filter-records"
                color="primary"
                type="submit"
              >
                Apply Filters
              </Button>
            </>
          }
        />
      </>
    );
  }
);

export default FilterDtComplaintAdminView;

const StyledFilter = styled.div`
  /* text-align: center; */

  .filter-main-title {
    padding: 0.2em 0;
    /* border-bottom: 0.1em solid rgba(0, 0, 0, 0.15); */
    font-weight: 900;
    margin: 0.5em 0;
    opacity: 0.37;
    text-align: start;
  }
`;
