import { Button } from "@material-ui/core";
import React, { memo, FC, useEffect, useState } from "react";
import styled from "styled-components";
import CircularLoadingProgress from "../../../Component/CircularLoadingProgress";
import { FTP_BASE_URL } from "../../../Helpers/Constants";
import NewsApi from "../../../Services/Api/NewsApi";
import { NewsFileModel } from "../../../Services/Models/NewsModels";
interface INewsFilesDIalog {
  news_pk: number;
}

export const NewsFilesDialog: FC<INewsFilesDIalog> = memo(({ news_pk }) => {
  const [fetching_files, set_fetching_files] = useState(false);

  const [files, set_files] = useState<Array<NewsFileModel>>([]);

  useEffect(() => {
    let mounted = true;
    const fetch_files = async () => {
      set_fetching_files(true);
      const response = await NewsApi.getNewsFiles(news_pk);

      if (response.success && mounted) {
        set_files(response.data);
      }

      mounted && set_fetching_files(false);
    };

    mounted && fetch_files();
    return () => {
      mounted = false;
    };
  }, [news_pk]);

  return (
    <StyledNewsFilesDialog>
      {/* <div className="button">
        <Button>Ipakita ang 4 ka mga files</Button>
      </div> */}
      {fetching_files ? (
        <CircularLoadingProgress />
      ) : (
        <div className="files">
          {files.map((file, index) => (
            <div key={index} className="file">
              {file?.mimetype?.includes("image") && (
                <img key={index} alt="" src={FTP_BASE_URL + file?.file_path} />
              )}
              {file?.mimetype?.includes("video") && (
                <video
                  key={index}
                  src={FTP_BASE_URL + file?.file_path}
                  controls
                ></video>
              )}
            </div>
          ))}
        </div>
      )}
    </StyledNewsFilesDialog>
  );
});

export default NewsFilesDialog;

const StyledNewsFilesDialog = styled.div`
  margin-top: 1em;
  .files {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    display: grid;
    grid-gap: 1rem;
    justify-content: start;
    align-content: flex-start;
    .file {
      img,
      video {
        height: 120px;
        width: 100%;
      }
    }
  }
`;
